.card{
    width: 10rem;
    height: 13rem;
    position: absolute;
    
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--orangeCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    padding: 0px 26px 2rem 26px;
}
.card span:nth-of-type(2){
    color: var(--grey);
    font-size: 16px;
}
.card > img{
    transform: scale(0.6);
    margin-bottom: -3rem;
}
.cbutton{
background-color: #ffffff;
box-shadow: 0px 19px 60px rgba(0, 0, 0, 0.08);
border-radius: 7px;
border: none;
padding: 10px;
font-size: 16px;
color: #5290fd;
}

@media screen and (max-width: 480px) {
    .card{
        width: auto;
    }
}